.swiper-container, .swiper-wrapper, .swiper-slide {
    -webkit-transform-style:preserve-3d;
    transform-style:preserve-3d;
}
.swiper-container {
    -webkit-perspective:1200px;
    -o-perspective:1200px;
    perspective:1200px;
}
.swiper-wrapper {
    /* Windows 8 IE 10 fix */
    -ms-perspective:1200px;
}

